.contacts{
    background-color:rgb(17, 17, 17);
    padding:40px 0;
    z-index: 2;
}

.contacts__header{
    color:white;
    font-family: 'Montserrat';
}

.contacts__info-list{
    margin:auto 0;
    padding:0 0 0 100px;
    display:flex;
    flex-direction:column;
    gap:35px;
}

.contacts__info-element{
    color:white;
    text-align: left;
    font-size: 20px;
    
}

.contacts__link{
    text-decoration: none;
    color: white;
    transition:opacity linear 0.1s;
}

.contacts__link:hover{
    opacity:0.7;
}

.contacts__data-container{
    display:flex;
    flex-direction:row-reverse;
    justify-content:center;
    padding:40px 0;
    margin:0 auto;
    width:85%;
}

.contacts__map{
    height:300px;
    width: 50%;
    padding-right:100px;
    border-right:1px solid white;
}

.leaflet-container{
    width: 100%;
    height: 100%;
    border-radius:10px;
    z-index:2;
  }

  @media screen and (max-width:768px) {

    .contacts{
        padding-top:10px;
    }

    .contacts__header{
        font-size: 18px;
    }

    .contacts__data-container{
        flex-direction:column-reverse;
        padding: 10px 0 0;
    }

    .contacts__info-list{
        padding:25px 0 0 25px;
        gap:20px;
    }

    .contacts__info-element{
        font-size: 14px;
    }

    .contacts__map{
        width:100%;
        height:200px;
        padding-right:0;
        border:none;
    }
  }